module.exports = "/* Style the header with a grey background and some padding */\r\n.header {\r\n    overflow: hidden;\r\n    background-color: #f1f1f1;\r\n    padding: 10px 10px;\r\n  }\r\n/* Style the header links */\r\n.header a {\r\n    float: left;\r\n    color: black;\r\n    text-align: center;\r\n    padding: 12px;\r\n    text-decoration: none;\r\n    font-size: 18px;\r\n    line-height: 25px;\r\n    border-radius: 4px;\r\n    margin : 10px;\r\n  }\r\n/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */\r\n.header a.logo {\r\n    font-size: 25px;\r\n    font-weight: bold;\r\n  }\r\n/* Change the background color on mouse-over */\r\n.header a:hover {\r\n    background-color: rgb(180, 179, 179);\r\n    color: black;\r\n  }\r\n/* Style the active/current link*/\r\n.header a.active {\r\n    background-color: dodgerblue;\r\n    color: white;\r\n  }\r\n/* Float the link section to the right */\r\n.header-right {\r\n    float: right;\r\n  }\r\n/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */\r\n@media screen and (max-width: 500px) {\r\n    .header a {\r\n      float: none;\r\n      display: block;\r\n      text-align: left;\r\n    }\r\n    .header-right {\r\n      float: none;\r\n    }\r\n  }\r\n.book_logo{\r\n    width: 70px;\r\n  }\r\n.author_logo{\r\n    width: 77px;\r\n  }\r\n.order_logo{\r\n    width: 85px; \r\n  }\r\n.shop_logo{\r\n    width: 85px; \r\n  }\r\n.sing_in{\r\n    width: 45px;\r\n    height: 77px;    \r\n  }\r\n.setting{\r\n    width: 105px;  \r\n    height: 100px;\r\n  }\r\n.my_order{\r\n    width: 75px;  \r\n\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */"